@use  "_mixins" as *;
@use  "_variables" as *;

* {
  @include styleReset;
}
button{
    @include clearButton;
}
a{
    text-decoration: none;
}
body.dark{
    background-color: rgba(23, 23, 30, 1);
}
header{
    display: flex;
    justify-content: space-between;
    padding: 20px 70px 30px 36px;
    gap: 10vw;
    .header_start{
        display: flex;
        justify-content: space-between;
        .header_start_nav{
            display: flex;
            align-items: center;
            .header_start_burger{
                display: none;
                @media (width < 1600px){
                    display: flex;
                    margin: 0px 35px;
                }
                @media (width < 500px){
                    display: none;
                }
            }
        }
    }
    .header_center{
        display: flex;
        justify-content: center; 
        flex: 1;
        .header_search{
            @include alignCenter;
            justify-content: space-between;
            max-width: 830px;
            min-width: 10px;
            width: 100%;
            height: 44px;
            background-color: $light_grey_bg;
            border-radius: 22px;
            padding-left: 20px;
            padding-right: 20px;
            flex-shrink: 1;
            @media (width < 556px){
                background-color: transparent;  
            }
            @media(width < 500px){
                padding-left: 9vw;
                padding-right: 0vw;
            }
            .header_search_input{
                background-color: transparent;
                border: none;
                width: 100%;
                height: 100%;
                @include textMain;
                @media (width < 556px){
                    display: none;  
                }
            }
            .header_search_input.dark::placeholder{
                @include textMain;
            }
            .header_search_btn{
                background-image: url("../images//loope.png");
                background-repeat: no-repeat;
                width: 19px;
                height: 19px;
                margin-left: 20px;
                flex-shrink: 0;
            }
        }
    }
    .header_end{
        @include alignCenter;
        gap: 30px;  
        .header_end_btns{
            @include alignCenter;
            justify-content: space-between;
            width: 175px;
            @media (width < 870px){
                display: none;    
            }
            .header_end_btns_cam{
                width: 27px;
                height: 20px;
            }
            .header_end_btns_dots{
                width: 21px;
                height: 21px;
            }
            .header_end_btns_notif{
                @include alignCenter;
                .header_end_btns_notif_count.dark{
                    @include alignCenter;
                    @include textSmall;
                    justify-content: center;
                    width: 18px;
                    height: 18px;
                    background-color: red;
                    border: 1px solid white;
                    border-radius: 15px;
                    transform: translate(-14px, -7px)
                }
            }
        }
        .header_icon_userpic{
            width: 40px;
            height: 40px;
        }
    }
}
main{
    display: flex;
    .side_nav{
        @include flexCol;
        margin-top: 30px;
        margin-left: 31px;
        gap: 60px;
        width: 160px;
        @media (width < 1600px){
            gap: 15px;
        }
        @media (width < 950px){
            display: none;
        }
        .side_nav_subs{
            @include flexCol;
            @media(width < 1600px){
                display: none;
            }
            .side_nav_subs_head.dark{
                @include textBold;
                padding-bottom: 15px;
            }
            .side_nav_subs_prof{
                @include alignCenter;
                gap: 10px;
                min-width: 0;
                padding: 12px 0px 12px 0px;
                .side_nav_subs_prof_name.dark{
                    @include textMain;
                    min-width: 0;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                }
            }
            .side_nav_subs_prof:hover{
                background-color: $light_white_bg;
                border-radius: 5.5px;
            }
        }
        .side_nav_sett_box{
            display: flex;
            flex-direction: column;
            gap: 25px;
            .theme_btn{
                margin-left: 1vw;
                position: relative;
                width: 61px;
                height: 31px;
                background: rgba(216, 215, 215, 0.5);
                display: flex;
                align-items: center;
                border-radius: 20px;
                border: 2px solid red;
                @media (width < 1600px){
                   margin-left: 2vw;
                }
                .theme_icon{
                    display: flex;
                    margin-left: 5px;
                    gap: 15px;
                }
            }
            .theme_btn .fill {
                position: absolute;
                right: 3px;
                top: 2px;
                border-radius: 20px;
                border: 1px solid red;
                width: 25px;
                height: 25px;
                transition: 0.4s right;
                background-color: red;
            }
            .theme_btn input {
                visibility: hidden;
            }   
            .theme_btn input:checked + span.fill {
                right: calc(100% - 29px);
            }
        }   
    }
    .feed{
        margin-left: 70px;
        display: flex;
        flex-direction: column;
        gap: 50px;
        @media (width < 620px){
            align-items: center;
            width: 100%;
            margin-left: 0px;
        }
        .channel_review{
           overflow: hidden; 
            display: flex;
            flex-direction: column;
            gap: 30px;
             @media (width < 620px){
                align-items: center;
                overflow: visible; 
                margin-bottom: 100px;
            }
            @media (width < 500px){ 
                margin-bottom: 135px;
            }
            .channel_review_start{
                width: 79vw;
                justify-content: space-between;
                @include alignCenter;
                @media (width < 1870px){
                    width: 70vw;
                }
                @media (width < 500px){
                    display: none;
                }
                .channel_review_prof{
                    @include alignCenter;
                    .channel_review_prof_name.dark{
                        @include textBoldH;
                        padding-left: 20px; 
                    }
                }
            }
        }
        .recommended{
            @media (width < 620px){
                display: none;
            }
            .recommended_start{
                display: flex;
                justify-content: space-between;
                width: 79vw;
                @media (width < 1870px){
                    width: 70vw;
                }
                @include alignCenter;
                .recommended_head.dark{
                    @include textBoldH;
                    padding-bottom: 30px;
                }
            }
            .recommended_content_boxes{
                display:flex;
                gap: 40px;
                .recommended_content_box{
                    .recommended_content_name.dark{
                        @include textMainBold;
                        padding-top:10px;
                    }
                    .recommended_content_details_box.dark{
                        @include detailsBox; 
                        font-size: 12px;
                        justify-content: space-between;
                        .recommended_content_details_box_start{
                            display: flex;
                            gap:15px;
                        }
                    }
                }
            }
        }
        .rec_channel{
            display: flex;
            flex-direction: column;
            gap: 30px;
            @media (width < 620px){
                display: none;
            }
            .rec_channel_start{
                display: flex;
                justify-content: space-between;
                width: 79vw;
                 @media (width < 1870px){
                    width: 70vw;
                }
                .rec_channel_prof{
                    display: flex;
                    align-items: center;
                    gap: 41vw;
                     @media (width < 1870px){
                        gap: 25vw;
                    }
                     @media (width < 1600px){
                       gap: 17vw;
                    }
                    @media (width < 1350px) {
                        flex-direction: column;
                        gap: 20px;
                        align-items: start;
                    }
                    .rec_channel_prof_box{
                        display: flex;
                        align-items: center;
                        gap: 10px;
                        .rec_channel_prof_name.dark{
                        @include textBoldH;
                        }
                        .rec_channel_prof_text.dark{
                            @include textSubMain2;
                            padding-top: 7px;
                             @media (width < 840px){
                                display: none;
                            }
                        }
                    }
                    .subscription_btn.dark{
                        width: 154px;
                        height: 40px;
                        background-color: red;
                        border-radius: 20px;
                        @include textMainSub;
                   }
                }
                .slider_box{
                    @include alignCenter;
                    gap: 40px;
                }
            }
        }
        .menuMedia_box{
            display:none;
            @media (width < 500px){
                display: flex;
                position: fixed;
                bottom: 0;
                background-color: white;
                justify-content: center;
                align-items: center;
                gap: 12vw;
                width: 100%;
                height: 61px;
                border-top: 1px solid $border;
                .menuMedia_btn.dark{
                    @include textMediaMenu;
                    @include flexCol;
                    align-items: center;
                    gap: 5px;
                    .side_nav_btn_img{
                        color: $menuMedia;
                        opacity: 0.6;
                    }
                }
            } 
        }
    }
}
#side_nav_btn_img_home{
    color: red;
}
#home{
    color: red;
}
.side_nav_main, .side_nav_categ, .side_nav_sett{
    @include flexCol;
    gap: 30px;
    .side_nav_btn{
        @include alignCenter;
        gap: 14px;
        .side_nav_btn_text.dark{
            @include textMain;
        }
    }
} 
.side_nav_categ{
    @media(width < 1600px){
        display: none;
    }
}
.side_nav_btn, .side_nav_sett{
    @media(width < 1600px){
        flex-direction: column;
        gap: 5px;
    }
}
.side_nav_sett{
    margin-top: 20px;
}
.slider_arrow{
    margin-right: 7px;
}  
.slider{
    @media (width < 620px){
        display: none;
    }
}
.slider_arrow{
    margin-right: 7px;
}  
.channel_review_content{
    display: flex;
    gap: 20px;
    width: clamp(320px, 90vw, 3000px);
    transition: transform 0.5s ease;
    @media (width < 620px){
        flex-direction: column;
        width: clamp(320px, 30vw, 3000px);
        gap: 90px;
    }
    .channel_review_content_box{
        @media (width < 620px){
            width: 100%;
            height: 170px;
        }
        .channel_review_content_video{
            @media (width < 620px){
                width: 100%;
                height: 100%;
            }
        }
        .channel_review_content_name.dark{
            @include textMainBold;
            padding-top:10px;
        }
        .channel_review_content_details_box.dark{
            @include detailsBox; 
            justify-content: space-between; 
        }
    }
}
